<template>
  <div :aria-valuetext="message.saved" v-if="!message.saved">
    <div
      v-for="(input, index) in message.inputs"
      :key="index"
      class="input-div"
    >
      <div class="input-placeholder">
        {{ capitalizeName(input.placeholder) }}
      </div>
      <div class="input-container">
        <form @submit.prevent="handleInputSubmit(input.placeholder, input.id)">
          <input
            required
            :type="input.type"
            autocomplete="off" 
            value=""
            :aria-label="input.placeholder"
            ref="qInput"
            v-model="inputField"
          />
          <button v-if="input" type="submit" aria-label="Submit" tabindex="0">
            <span aria-hidden="true" class="">
              <svg
                xmlns="http://www.w3.org/2000/svg"
                width="16"
                height="16"
                viewBox="0 0 16 16"
                fill="none"
              >
                <path
                  d="M5.42773 4.70898C5.46387 4.85254 5.53809 4.98828 5.65039 5.10059L8.54932 8L5.64893 10.9004C5.31689 11.2324 5.31689 11.7705 5.64893 12.1025C5.98096 12.4336 6.51904 12.4336 6.85107 12.1025L10.3516 8.60059C10.5591 8.39355 10.6367 8.10449 10.585 7.83691C10.5537 7.67578 10.4761 7.52246 10.3516 7.39844L6.85254 3.89941C6.52051 3.56738 5.98242 3.56738 5.65039 3.89941C5.43066 4.11816 5.35645 4.42871 5.42773 4.70898Z"
                  fill="#ffffff"
                ></path>
              </svg>
            </span>
          </button>
        </form>
      </div>
    </div>
  </div>
  <div :aria-valuetext="message.saved" v-else>
    <div :key="index" class="input-div">
      <div class="input-placeholder">
        {{ capitalizeName(message.placeholder) }}
      </div>
      <div class="input-container">
        <input
          disabled
          required
          :type="message.type"
          autocomplete="off"
          :value="message.value"
          :aria-label="message.placeholder"
        />
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data: () => ({
    inputField: "",
    placeholder: "",
  }),
  props: ["message"],
  methods: {
    async handleInputSubmit(placeholder, id) {
      console.log(this.inputField, placeholder);

      const value = `My ${placeholder} is: ${this.inputField}`;

      console.log(this.message, this.inputField, value);

      this.$emit("handleInputSubmit", {
        id,
        msg: this.message,
        inputValue: this.inputField,
        placeholder: placeholder,
        value: value,
      });
    },

    capitalizeName(name) {
      console.log(name, "the name");
      return name.charAt(0).toUpperCase() + name.slice(1);
    },
  },
};
</script>
