import { createApp } from "vue";
import testApp from "./testApp.vue";
import vuetify from "./plugins/vuetify";

const shadowHost = document.querySelector("#chatly-app");
const shadowRoot = shadowHost.attachShadow({ mode: "open" });
shadowHost.style.display = "none";

// Fetch the CSS file
fetch("https://joshijax.github.io/chatlywidget/css/app.css")
  .then(function (response) {
    return response.text();
  })
  .then(function (cssText) {
    // Create a style element and set its content to the CSS text

    const styleElement = document.createElement("style");
    styleElement.textContent = cssText;

    fetch("https://joshijax.github.io/chatlywidget/css/chunk-vendors.css")
      .then(function (response) {
        return response.text();
      })
      .then(function (cssText) {
        // Create a style element and set its content to the CSS text

        // Append the style element to the Shadow Root

        // Create the Vue app and mount it within the Shadow Root
        createApp(testApp).use(vuetify).mount(shadowRoot);

        shadowRoot.prepend(styleElement);

        const styleElement1 = document.createElement("style");
        styleElement1.textContent = cssText;
        shadowRoot.prepend(styleElement1);

        shadowHost.style.display = "block";
      });
  });

